import Vue from 'vue'
import App from './App.vue'
import vuescroll from 'vuescroll';
import 'vuescroll/dist/vuescroll.css';
import {Checkbox, Radio, Select, Option, Loading} from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import '@/assets/flexible.js'

Vue.use(vuescroll);
Vue.use(Loading);
Vue.use(Option);
Vue.use(Select);
Vue.use(Checkbox);
Vue.use(Radio);
Vue.prototype.$vuescrollConfig = {
    bar: {
        keepShow: true,
        background: "#DDE6EE"
    }
};

Vue.config.productionTip = false

new Vue({
    render: h => h(App),
}).$mount('#app')