<template>
    <div id="app">
        <div class="page_wrap">
            <div class="compare">
                <div class="single" v-if="selectedTotal===1">
                    <div class="board">
                        <img :src="blackBoard" width="100%" />
                    </div>
                    <div class="answer"
                        @dblclick="checkTrails(records[currSelectedIndex].blockKey,records[currSelectedIndex].noteKey,records[currSelectedIndex].pageImgUrl,records[currSelectedIndex].deviceAlias)">
                        <img :src="records[currSelectedIndex].pageImgUrl+'?x-oss-process=image/resize,h_'+imgHeight" />
                        <div class="stuName">{{records[currSelectedIndex].deviceAlias}}</div>
                    </div>
                </div>
                <vue-scroll v-else>
                    <div class="selectedList" :style="{marginTop:selectNum==2?'-2.5%':selectNum==4?'2%':'0'}">
                        <template v-for="(item,index) in records">
                            <div class="selectedStu" :id="item.deviceMac" v-if="item.selected"
                                :style="{width:selectNum==2?'25vw':selectNum==4?'22vw':100/selectNum+'vw',margin:selectNum==2?'5vw':selectNum==4?'0.5vw':'0',padding:selectNum>4?5/selectNum+'vw':'0'}" :key="item.deviceMac"
                                @dblclick="checkTrails(item.blockKey,item.noteKey,item.pageImgUrl,item.deviceAlias)">
                                <img :src="item.pageImgUrl+'?x-oss-process=image/resize,h_'+imgHeight"
                                    :alt="item.deviceAlias" />
                                <div class="stuName">{{item.deviceAlias}}</div>
                            </div>
                        </template>
                    </div>
                </vue-scroll>

            </div>

            <div class="options">
                <div class="selectedNum">已选{{selectedTotal}}/{{records.length}}</div>
                <div class="checkAll">
                    <el-checkbox v-model="checkAll" @change="selectAll">全选</el-checkbox>
                </div>
                <div class="checkmult">
                    <el-checkbox v-model="checkMulti" @change="selectMulti">多选</el-checkbox>
                </div>
                <div class="compNum">单行数量&nbsp;&nbsp;<el-select size="mini" v-model="selectNum" placeholder="请选择">
                        <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select>
                </div>
            </div>

            <div class="thumbs">
                <vue-scroll :ops="ops">
                    <div class="stuList">
                        <div class="student"  v-for="(item,index) in records"
                            :key="item.deviceMac" @click="selectStu(index)">
                            <div class="stuImg">
                                    <el-checkbox class="radio" :value="item.selected" ></el-checkbox>
                                <a :href="'#'+item.deviceMac">
                                    <img :src="item.pageImgUrl+'?x-oss-process=image/resize,h_'+imgHeight" />
                                </a>
                            </div>
                            <div class="name">{{item.deviceAlias}}</div>
                        </div>
                    </div>
                </vue-scroll>
                <div class="btnGroup btnGroupL">
                    <div class="stop"><img src="@/assets/icon_stop@2x.png"/>停止</div>
                    <div class="finish"><img src="@/assets/icon_end@2x.png" />结束</div>
                    <div class="close" @click="closeWindow"><img src="@/assets/icon_hide@2x.png" />关闭</div>
                </div>
                <div class="btnGroup btnGroupR">
                    <div class="stop"><img src="@/assets/icon_stop@2x.png" />停止</div>
                    <div class="finish"><img src="@/assets/icon_end@2x.png" />结束</div>
                    <div class="close" @click="closeWindow"><img src="@/assets/icon_hide@2x.png" />关闭</div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import { formatDate } from '@/assets/date.js';
    import utils from '@/assets/utils.js';
    import API from '@/assets/api.js'
    import axios from 'axios'
    var timer;
    export default {
        name: 'App',
        data() {
            return {
                taskId: "",
                ops: {
                    scrollPanel: {
                        scrollingX: true,
                        scrollingY: false
                    }
                },
                options: [{
                    value: 2,
                    label: '2'
                }, {
                    value: 4,
                    label: '4'
                }, {
                    value: 8,
                    label: '8'
                }],
                blackBoard: "",
                selectNum: 2,
                currSelectedIndex: 0,
                checkAll: false,
                checkMulti: true,
                records: []
            }
        },
        mounted() {
            //注册需要外部调用的方法
            window.setPercent = this.setPercent
            //获取url参数
            let taskId = utils.getUrlPrams("taskId")
            this.taskId = taskId
            console.log(taskId)
            this.getTrails()
            this.getBlack(taskId)
            this.$once('hook:beforeDestroy', () => {
                clearTimeout(timer);
            })
        },
        computed: {
            selectedTotal: function () {
                let n = 0;
                for (const item of this.records) {
                    if (item.selected) {
                        n++
                    }
                }
                return n;
            },
            recordsTaskIds: function () {
                let arr = []
                for (const task of this.records) {
                    arr.push(task.deviceMac)
                }
                return arr
            },
            imgHeight: function () {
                return parseInt(window.innerHeight * 0.8)
            },
        },
        methods: {
            selectStu(index) {
                console.log(this.selectedTotal)
                this.currSelectedIndex = index
                if (this.checkAll) {
                    this.checkAll = !this.checkAll
                }
                if (this.checkMulti) {
                    if (this.selectedTotal === 1 && this.records[index].selected) {
                        console.log("只剩一个了")
                    } else {
                        this.records[index].selected = !this.records[index].selected
                    }

                } else {
                    for (let i = 0; i < this.records.length; i++) {
                        if (i === index) {
                            this.records[i].selected = true
                        } else {
                            this.records[i].selected = false
                        }
                    }
                }
                if(this.selectedTotal==this.records.length){
                    this.checkAll = true
                }

            },
            selectAll(checked) {
                if (checked) {
                    for (let i = 0; i < this.records.length; i++) {
                        this.records[i].selected = true
                    }
                    this.checkMulti = true
                }

            },
            selectMulti(checked) {
                if (!checked) {
                    for (let i = 0; i < this.records.length; i++) {
                        if (i === this.currSelectedIndex) {
                            this.records[i].selected = true
                        } else {
                            this.records[i].selected = false
                        }
                    }
                    this.checkAll = false
                }
            },
            checkTrails(bk, nk, img, name) {
                let currStuInfo = [{
                    blockKey: bk,
                    noteKey: nk,
                    recogImgUrl: img,
                    imgUrl: "",
                }]
                localStorage.setItem('currStuInfo', JSON.stringify(currStuInfo));
                window.cefQuery({
                    request: "checkTrailsInteraction:" + 0 + "&" + name,   // 代表请求方法 example事件名称 data 为传入参数
                    onsuccess: function (response) {
                        console.log("invoke success response = " + response);
                    },
                    onFailure: function (ecode, emsg) {
                        console.log("invoke fail code = " + ecode + " msg:" + emsg);
                    }
                });
            },
            closeWindow() {
                window.cefQuery({
                    request: "closeWindowInteration",   // 代表请求方法 example事件名称 data 为传入参数
                    onsuccess: function (response) {
                        console.log("invoke success response = " + response);
                    },
                    onFailure: function (ecode, emsg) {
                        console.log("invoke fail code = " + ecode + " msg:" + emsg);
                    }
                });
            },
            getBlack(id) {
                var that = this
                axios.get(API.api_tl + 'api/selBlackImg', {
                    params: {
                        taskId: id
                    }
                }).then(function (res) {
                    // console.log(res.data)
                    that.blackBoard = res.data.data

                }).catch(function (error) {
                    console.log(error);
                });
            },
            getTrails() {
                var that = this
                axios.get(API.api_qb + 'progress/trailsRecord', {
                    params: {
                        taskId: that.taskId
                    }
                }).then(function (res) {
                    console.log(res.data.data)
                    let records = res.data.data.trailsRcords
                    let count = res.data.data.deviceAmount

                    if (that.records.length > 0) {
                        for (let i = 0; i < records.length; i++) {
                            if (that.recordsTaskIds.indexOf(records[i].deviceMac) < 0) {
                                records[i].selected = false
                                that.records.push(records[i])
                            }
                        }
                    } else {
                        for (let i = 0; i < records.length; i++) {
                            if (i === that.currSelectedIndex) {
                                records[i].selected = true
                            } else {
                                records[i].selected = false
                            }
                        }
                        that.records = records
                    }

                    if (count !== records.length) {
                        timer = setTimeout(() => {
                            that.getTrails()
                        }, 2000);
                    }
                    console.log(that.records)
                }).catch(function (error) {
                    console.log(error);
                });
            }
        }
    }
</script>
<style>
    * {
        margin: 0;
        padding: 0;
    }

    body {
        background: #29435C;
    }

    #app {
        font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-align: center;
        color: #2c3e50;
    }

    .el-select {
        width: 0.6rem;
        
    }
    
    .el-input__inner{
        border:0.01rem solid #414F5D !important;
    }

    .el-checkbox__label {
        color: #fff;
    }

    .el-checkbox__inner:hover {
        border-color: #414F5D !important;
    }

    .el-select:hover .el-input__inner {
        border: 0.01rem solid #414F5D!important;
    }

    .el-select .el-input__inner:focus {
        border-color: #414F5D!important;
    }

    .el-select:hover .el-input__inner {
        border-color: #414F5D!important;
    }

    .el-select:hover .el-input__inner {
        border: 0.01rem solid #414F5D;
    }

    .el-select .el-input__inner:focus {
        border-color: #414F5D!important;
    }

    .el-checkbox__input.is-checked .el-checkbox__inner {
        background-color: #091722 !important;
        border-color: #414F5D !important;
    }

    .el-checkbox__input.is-checked+.el-checkbox__label {
        color: #fff !important;
    }

    .el-checkbox__input.is-focus .el-checkbox__inner {
        border-color: #556B81 !important;
    }

    .el-select-dropdown {
        border: 0.01rem solid #030A10 !important;
        background-color: #030A10 !important;
    }

    .el-checkbox__inner {
        background-color: #091722 !important;
        border: 0.01rem solid #414F5D !important;
    }

    .el-input__inner {
        background-color: #091722 !important;
        color: #fff !important;
    }

    .el-select-dropdown__item.hover,
    .el-select-dropdown__item:hover {
        background-color: #091722 !important;
    }

    .el-select-dropdown__item.selected {
        color: #fff !important;
    }

    .el-popper[x-placement^=top] .popper__arrow::after{
        border-top-color: transparent!important;
    }

    .el-popper[x-placement^=top] .popper__arrow{
        border-top-color: transparent!important;
    }
    .el-popper[x-placement^=bottom] .popper__arrow::after{
        border-bottom-color: transparent!important;
    }

    .el-popper[x-placement^=bottom] .popper__arrow{
        border-bottom-color: transparent!important;
    }
    .radio .el-checkbox__input.is-checked .el-checkbox__inner{
        background-color: #00D97F !important;
        border-color: #00D5A3 !important;
        /* border-radius: 50%!important; */
    }
    .radio .el-checkbox__inner {
        background-color: #F2F5FB !important;
        border: 0.01rem solid #AABBD9 !important;
        border-radius: 50%!important;
    }

    .el-checkbox__label{
        padding-left: 0.1rem!important;
        line-height: 0.19rem!important;
        font-size: 0.14rem!important;
    }
    .el-checkbox__inner{
        width: 0.14rem!important;
        height: 0.14rem!important;
        border-radius: 0.02rem!important;
    }
    .el-checkbox__inner::after{
        border-width: 0.01rem!important;
        height: 0.07rem!important;
        left: 0.04rem!important;
        top: 0.01rem!important;
        width: 0.03rem!important;
    }
    .el-input--mini .el-input__inner{
        height: 0.28rem!important;
        line-height: 0.28rem!important;
    }
    .el-input--suffix .el-input__inner{
        padding-left: 0.15rem!important;
    }
    .el-input__inner{
        border-radius: 0.04rem!important;
        padding: 0 0.15rem!important;
        font-size: 0.12rem!important;
    }
    .el-select-dropdown__item{
        font-size: 0.14rem!important;
        padding: 0 0.2rem!important;
        height: 0.34rem!important;
        line-height: 0.34rem!important;
    }
</style>
